.card {
    margin-top: 10px;
    margin-left: 20px;
    height: 375px;
    width: 250px;
    background: black;
    border-radius: 10px;
    display: grid;
}
.card-atas {
    background:url('../Picture/midstory-pict/DCBAE103-D4A3-4F33-AD67-5062BC76D4C9.JPG');
    background-size: 199px;
    height: 335px;
    width: 220px;
    margin-left: 15px;
    margin-top: 15px;
    border-radius: 5px;
    position: relative;
}
.card-bottom {
    color: red;
    text-align: center;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
